import React from 'react';
import './AboutMe.css';
import { aboutMe } from '../../portfolio';

function Hello() {
	const Fade: any = require("react-reveal/Fade");
	return (
		<Fade duration={800} bottom>
			<section className="section--about" id="aboutMe">
				<div className="inner">

					<div className="wrapper">
						<div className="title">
							<span>{aboutMe.title}</span>
						</div>
						<div className="text-subtitle">
							<p className='text-content'>
								배우고자 하는 호기심을 적극적으로 탐구하고 끊임없이 <br/>
								성장할 수 있는 일을 하고자 개발자를 희망하게 되었습니다.<br/>
								남들이 쉽게 이해하고 유지보수할 수 있는 깔끔한 코드를 <br/>
								작성하는 것을 중요하게 생각합니다.<br/><br/>
							</p>

								<li>오늘 배운 내용을 복습하고 정리하는 습관을 가지기 위해 TIL 작성</li>
								<li>Java, API 데이터를 활용한 DB관리, 관련 프로젝트 경험</li>
								<li>jQury, Ajax, Spring F/W 등 다양한 기능 활용한 개발 경험</li>
						</div>
					</div>

				</div>
			</section>
		</Fade>
	);
}
export default Hello;